import { CirclePicker } from 'react-color'
import {
    Modal
} from '@buildo/bento-design-system'
import { useTranslation } from 'next-export-i18n';

import { useState } from 'react'
import useDeviceContext from '@/hooks/useDeviceContext';
import { produce } from 'immer'
import { IRGBParam } from '@/gateway/step';


export const ColorRune = ({ index }: { index: number }) => {
    /**
     * This function displays the input text as normal text, but word by word so it will wrap around.
     * @param {string} text - The text to be displayed.
    */
    
    const [colorModal, setColorModal] = useState(false)
    const { t } = useTranslation()
    const { formulas, setFormulas, selectedFormulaId } = useDeviceContext()
    const step = formulas[selectedFormulaId].steps[index];
    const action = step.action as IRGBParam;


    return (
        <>
            {colorModal && (
                <Modal
                size="small"
                autoFocus={false}
                title={t("Set color")}
                onClose={() => setColorModal(false)}
                primaryAction={{
                    label: t("OK"),
                    onPress: async () => {
                        setColorModal(false)

                    },
                }}
                secondaryAction={{
                    label: t("Cancel"),
                    onPress: () => setColorModal(false),
                }}
            >
                <div className="flex items-center justify-center w-full h-full p-4">
                    <CirclePicker
                        className='m-4'
                        color={action.color || "#00aeef"}
                        onChange={(color: any, e: any) => {
                            // setFormulas(produce(formulas, draft => {
                            //     if (!formulas[selectedFormulaId]) return;
                            //     formulas[selectedFormulaId]. = color.hex
                            // }))
                            console.log("set to colr ", color)


                            setFormulas(produce(formulas, (draft) => {
                                // draft[selectedFormulaId].steps[index].target.value = e;
                                
                                var action = draft[selectedFormulaId].steps[index].action as IRGBParam
                                action.color = color.hex
                                action.start = 0
                                action.stop = 0
                            }))


                        }}
                    />
                </div>
            </Modal>
            )}
            <button className="ml-2 h-8 w-8  rounded-xl  ring-1 ring-white "
                style={{
                    backgroundColor: action.color || "#000000"
                }}
                onClick={() => {
                    setColorModal(true)
                }}
            ></button>

        </>
    );
};
